@import "../colors.scss";

.header-component {
  text-align: center;
  color: $white;

  .name-title {
    font-size: 80px;
    font-weight: bold;

    @media only screen and (min-device-width: 375px) and (max-device-width: 812px) and (-webkit-min-device-pixel-ratio: 3) {
      font-size: 144px;
    }
  }

  .degree-title {
    margin-top: 32px;
    font-size: 32px;
    color: $grey-header;
    font-weight: bold;
    padding: 0 40px;

    @media only screen and (min-device-width: 375px) and (max-device-width: 812px) and (-webkit-min-device-pixel-ratio: 3) {
      font-size: 48px;
    }
  }
}
